cal_text{display:none}}.big_body .container .social_media{color:#fff;position:absolute;font-size:25px;left:2%;bottom:2%;width:100px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.big_body .container .social_media .icons{transition:0.5s ease}.big_body .container .social_media .icons:hover{cursor:not-allowed;color:#fea85a;-webkit-transform:translateY(-10px);transform:translateY(-10px)}

